:root {
    --clr-text: #291707;
    --clr-title: #454a18;
    --clr-backgraund: lightgrey;
    --clr-backgraundbtn: grey;
    --clr-textbtn: black;
}

*{
    box-sizing: border-box;
    margin: 0;
}

body {
    width: 100vw;
    height: 100vh;
}

.title {
    color: var(--clr-title);
    font-style: italic;
    margin-left: 20px;
}

ol {
    width: 200px;
}

.note {
    background-color: var(--clr-backgraund);
    display: grid;
    align-items: center;
    grid-template-rows: 90px 90px;
    width: 200px;
    margin: 10px 0;
    font-family: 'Courier New', Courier, monospace;
    border-radius: 5px 5px 0 0;
}

.note_text {
    margin: 0 8px 0 8px;
}

/*botones */ 

.btn_flexPosition { 
    border-top: 1px solid var(--clr-textbtn);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

.note_btnChangeImportance {
    border: 0;
    padding: 3px;
    border-radius: 4px;
    width: 80%;
    font-size: 15px;
    font-weight: 400;
    font-style: italic;
    text-transform: capitalize;
    margin-top: 10px;
    border: 2.5px solid transparent;
}

.note_btnChangeImportance:hover {
    border: 2.5px solid var(--clr-backgraundbtn);
    background-color: var(--clr-textbtn);
    border-radius: 4px;
    color: white;
    cursor: pointer;
}

.btnImportanceColor {
    background-color: var(--clr-backgraundbtn);
    color: var(--clr-textbtn);
}

.note_btnDelete {
    border: 0;
    font-style: italic;
    font-weight: 400;
    font-size: 15px;
    text-transform: capitalize;
    padding: 3px;
    width: 80%;
    border: 2.5px solid var(--clr-backgraundbtn);
    border-radius: 4px;
}

.btnDeleteColor {
    background-color: var(--clr-backgraundbtn);
    color: var(--clr-textbtn);
}

.note_btnDelete:hover {
    border: 2.5px solid var(--clr-backgraundbtn);
    background-color: var(--clr-textbtn);
    border-radius: 4px;
    color: white;
    cursor: pointer;
}

.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}


.containerApp {
    display: flex;
    flex-wrap: wrap;
    min-width: 100%;
    height: 100%;
}



.header {
    display: flex;
    align-items: center;
    width: 50%;
    gap: 10%;
}


.containerButtonImportant {
    margin-top: 10px;
    display: flex;
    width: 100px;
    height: fit-content;
    gap: 20px;
}

.containerNotes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 800px;
    gap: 10px;
    min-height: 83.6vh;
    margin: 0 auto;
}

.containerForm {
    display: flex;
    margin-top: 10px;
    gap: 20px;

}


.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 10vh;
    color: green;
    background-color: black;
}